import React from "react";
import SeasonPresenter from "./SeasonPresenter";
import { moviesApi, tvApi } from "../../api";

export default class extends React.Component {
  constructor(props) {
    super(props);
    const {
      location: { pathname },
    } = props;
    this.state = {
      result: null,
      usResult: null,
      error: null,
      loading: true,
      isMovie: pathname.includes("/movie/"),
    };
    console.log(">>>props: ", props);
  }

  async componentDidMount() {
    const {
      match: {
        params: { id },
      },
      history: { push },
    } = this.props;

    const { isMovie } = this.state;
    console.log(isMovie);
    const parsedId = parseInt(id);
    if (isNaN(parsedId)) {
      return push("/");
    }
    let result = null;
    let usResult = null;
    try {
      if (isMovie) {
        ({ data: result } = await moviesApi.movieDetail(parsedId));
        ({ data: usResult } = await moviesApi.movieDetailUs(parsedId));
      } else {
        ({ data: result } = await tvApi.showDetail(parsedId));
        ({ data: usResult } = await tvApi.showDetailUs(parsedId));
      }
    } catch {
      this.setState({ error: "ê²°ê³¼ë¥¼ ì°¾ì„ ìˆ˜ ì—†ìŠµë‹ˆë‹¤." });
    } finally {
      this.setState({ loading: false, result, usResult });
    }
  }
  render() {
    const { result, error, loading, usResult } = this.state;
    console.log(">>>ìƒì„¸ ë°ì´í„°: ", this.state);
    return (
      <SeasonPresenter
        result={result}
        error={error}
        loading={loading}
        usResult={usResult}
      />
    );
  }
}
